:root {
    --cate-menu-width: 5%;
}

.parent {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.CategoryPanel {
    grid-area: category;
    display: grid;
    place-items: start;
    padding: 0 10%;
}

.products {
    grid-area: products;
    display: flex;
}

.link {
    text-decoration: none;
    font: 500 1rem sans-serif;
    color: black;
}

@media screen and (max-width: 600px) {
    .CategoryPanel {
        padding: 0% 4%;
    }
}