:root {
    --hight: 18%;
    --width: 90vw;
}

.parent {
    position: relative;
    height: var(--hight);
    width: 100%;
    padding-top: 1vh;
    z-index: 3;
    background-color: #fff;
    cursor: pointer;
    display: grid;
    grid-template-columns: 15% 60% 25%;
    grid-template-rows: 65% auto;
    grid-template-areas: "L S A " "M M M ";
    box-shadow: 0px 0.0125vh 3vh hsl(0, 0%, 0%, 30%);
}


/*PARENT CHILDREN SET GRID ARIEA*/

.logo {
    grid-area: L;
}

.search {
    grid-area: S;
}

.auth {
    grid-area: A;
}

.menu {
    grid-area: M;
}

.parent>* {
    display: flex;
    justify-content: center;
    align-items: center;
}


/**/


/*Shearch*/

.search input {
    width: 100%;
    height: 70%;
    border: solid 1px hsl(0, 0%, 0%, 50%);
    border-radius: 2rem;
    padding: 0 1%;
}

.search input:focus {
    outline: none;
}


/*mobile version menu button*/

.menu_button {
    position: fixed;
    right: 5%;
    top: 1%;
    z-index: 10;
    display: none;
}

.menu_button_icon {
    font-size: 2.3rem;
    color: #d0d0d0;
}


/**/

.menu ul {
    width: 50%;
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1%;
}

.menu ul li {
    height: 100%;
    flex-basis: 100%;
    cursor: pointer;
}

.auth {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 10%;
}

.social {
    display: none;
}

.login,
.singup {
    color: #969696;
    font-weight: 500;
    cursor: pointer;
}

.icon {
    font-size: 1.7rem;
}

.menu_link {
    height: 100%;
    color: #2e2e2e;
    font: 500 1rem sans-serif;
    display: grid;
    place-content: center;
}

.menu ul li:hover~.CategoryAutoSuggest {
    opacity: 1;
}

.CategoryAutoSuggest {
    opacity: 0;
    pointer-events: none;
    top: 19vh;
    position: absolute;
    height: 50vh;
    left: calc(calc(100vw - var(--width)) / 2);
    width: var(--width);
    background-color: white;
    box-shadow: 0px 8px 8px hsla(0, 0%, 0%, 0.5);
    display: grid;
    grid-template-columns: 30% auto;
    grid-template-rows: 1fr;
    grid-template-areas: "s c";
    transition: 300ms ease;
}

.CategoryAutoSuggest .side {
    grid-area: s;
    background-color: rgb(233, 233, 233);
}

.CategoryAutoSuggest .content {
    grid-area: c;
}

.side ul {
    width: 100%;
    padding: 5% 0;
    display: flex;
    gap: 1%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.side ul li {
    flex-basis: 10%;
    width: 80%;
}

@media screen and (max-width: 600px) {
    .parent {
        position: fixed;
        height: 100vh;
        width: 80%;
        top: 0%;
        right: -100%;
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 15% 60% 25%;
        grid-template-areas: "L" "M" "A";
        transition: 300ms ease;
        cursor: none;
    }
    .search {
        display: none;
    }
    .menu_button {
        display: block;
    }
    .parent.avtive {
        right: 0;
    }
    .login,
    .singup {
        display: none;
    }
    .logo {
        display: flex;
        justify-content: flex-start;
        padding: 0 10%;
    }
    .auth {
        padding: 0 10%;
    }
    .social {
        height: 100%;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 50%);
        grid-template-rows: 45% repeat(2, auto);
        grid-template-areas: "email email" "fb insta" "ebay ebay";
    }
    .fb,
    .insta,
    .ebay {
        width: 100%;
        font-size: 2.3rem;
        color: #5e5e5e;
    }
    .email {
        grid-area: email;
        font-size: 0.8rem;
    }
    .email p {
        width: max-content;
        border-bottom: 1px solid black;
    }
    .fb {
        grid-area: fb;
    }
    .ebay {
        grid-area: ebay;
    }
    .insta {
        grid-area: insta;
    }
    .menu ul {
        width: 100%;
        height: 70%;
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 6vh;
        padding: 8%;
        margin: 15%;
        border-left: 1px solid black;
    }
}