.parent {
    position: relative;
    width: 17vw;
    height: 27rem;
    border-radius: 0.4rem;
    box-shadow: 0px 5px 8px hsl(0, 0%, 0%, 0.1);
}

.img {
    width: 100%;
    height: 75%;
    overflow: hidden;
    position: relative;
    background-color: hsl(0, 0%, 88%, 30%);
    background-position: center;
}

.price {
    display: flex;
    justify-content: flex-start;
}

.img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 300ms ease;
}

.img:hover img {
    filter: blur(0.11rem);
}

.img:hover .hover_details {
    opacity: 1;
}

.hover_details {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 65% auto;
    grid-template-rows: 17% auto 25%;
    grid-template-areas: "s n" " nu nu" "c f";
    transition: 500ms ease;
    opacity: 0;
}

.hover_details>* {
    display: grid;
    place-items: center;
    cursor: pointer;
}

.hover_details>*>* {
    border-radius: 5px;
    color: lightslategray;
}

.state {
    grid-area: s;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 5%;
}

.add_cart {
    grid-area: c;
}

.fav {
    grid-area: f;
}

:root {
    --hight-size: 3vw;
}

.fav .icon {
    width: var(--hight-size);
    height: var(--hight-size);
    background-color: #fff;
    color: hsl(34, 58%, 60%);
    display: grid;
    place-content: center;
    font-size: 1.8rem;
}

.add_cart .cart {
    width: 90%;
    height: var(--hight-size);
    background-color: #fff;
    display: grid;
    place-content: center;
    font-size: 1rem;
    font-weight: 500;
    transition: box-shadow 300ms ease;
}

.add_cart .cart:hover {
    box-shadow: 0px 0.2em 0px lightgreen;
}

.state .s {
    width: 60%;
    height: calc(var(--hight-size) - 0.3vw);
    display: grid;
    color: rgb(0, 0, 0);
    place-content: center;
    font-size: 1rem;
    font-weight: 500;
}

.s.new {
    background-color: lightgreen;
}

.s.sell {
    background-color: lightcoral;
}

.data {
    height: 20%;
    margin: 0 2%;
    padding: 2% 0;
    display: grid;
}

.name {
    position: relative;
    width: 100%;
    height: 3vh;
    padding: 0;
    overflow: hidden;
    cursor: pointer;
}

.name_conatiner {
    position: relative;
}

.name_conatiner:hover {
    text-decoration: underline;
}

.toltips {
    visibility: hidden;
    width: max-content;
    background-color: hsl(0, 0%, 7%, 70%);
    color: #fff;
    text-align: center;
    border-radius: 0.3rem;
    padding: 0.8vh 0.5vw;
    position: absolute;
    z-index: 1;
    opacity: 0;
    top: 0%;
    transition: 00ms ease;
}

.name_conatiner:hover .toltips {
    visibility: visible;
    opacity: 1;
    transition: 800ms ease;
}

a {
    text-decoration: none;
}

.category>*>p {
    font: 300 0.3rem;
    color: rgb(158, 158, 158);
    cursor: pointer;
}

.category:hover p {
    text-decoration: underline;
}

@media screen and (max-width: 600px) {
     :root {
        --hight-size: 3rem;
    }
    .parent {
        width: 80%;
    }
    .price {
        justify-content: center;
        gap: 2vw;
        align-items: center;
        height: 3vh;
        padding: 0 5%;
    }
    .hover_details {
        opacity: 1;
    }
}