.parent {
    display: grid;
    height: 8rem;
    width: 70%;
    grid-template-columns: 50% 5% 55%;
    grid-template-rows: repeat(2);
    grid-template-areas: "PR n ST" "SO n S";
}

.parent>* {
    margin: 3% 0;
    display: flex;
    height: 3rem;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    justify-self: flex-start;
}

.price {
    grid-area: PR;
    justify-content: center;
    align-items: center;
    gap: 5%;
}

.shipingLocation {
    height: 70%;
    align-items: center;
}

.price>input,
.shipingLocationSelector {
    width: 6vw;
    height: 70%;
    border: 1px solid hsl(0, 0%, 0%, 30%);
    padding: 0 1%;
}

.shipingLocationSelector {
    height: 100%;
    width: 4rem;
}

.shipingLocation {
    grid-area: SL;
}

.state {
    grid-area: ST;
}

.state>* {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.Ratinng {
    grid-area: RT;
}

.Sort {
    grid-area: SO;
    gap: 5%;
    justify-content: flex-start;
}

.wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1%;
    background: #fff;
    border: 1px solid hsl(0, 0%, 0%, 10%);
    height: 3rem;
    width: 75%;
    justify-content: space-between;
    padding: 0% 2%;
}

.Sort>* {
    cursor: pointer;
}

.submit {
    grid-area: S;
}

.submit>input {
    width: 5vw;
    height: 3rem;
    border: none;
    background-color: rgb(10, 178, 235);
    color: #fff;
}

.check {
    display: flex;
    flex-basis: 100%;
}

.filter {
    display: none;
}

@media screen and (max-width: 600px) {
    .parent {
        display: flex;
        height: 25rem;
        padding: 2%;
        margin: 2rem 0;
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        gap: 3%;
        background-color: #eee;
        box-shadow: 1px 2px 8px hsl(0, 0%, 0%, 30%);
    }
    .parent>* {
        margin: unset;
        display: flex;
        align-items: center;
        justify-content: space-between;
        justify-self: flex-start;
    }
    .filter {
        display: flex;
    }
    .shipingLocationSelector {
        width: 100%;
        background-color: #fff;
        border: none;
    }
    .price :is(input, .shipingLocation) {
        width: 35%;
        height: 100%;
        border: 1px solid hsl(0, 0%, 0%, 30%);
        margin: 0 1%;
    }
    .submit>input {
        width: 60%;
    }
}